  import Bg2 from "@/assets/timg.jpg"
export default {
    data(){
      return{
        parms:{
          password:null,
          username:null,
        },
        bg2:Bg2,
        nametext:null,
        pastext:null
      }
    },
  methods:{
    login(){
      this.nametext=null,
        this.pastext=null
        if (this.parms.password&&this.parms.username){
          let formdata=new FormData()
          formdata.append("phone",this.parms.username)
          formdata.append("password",this.parms.password)
          this.$axios.post("/leaser/login",formdata).then((res)=>{
            if (res.data.code=="ok") {
              this.$router.push({
                path:"/List",
                query:{
                  id:res.data.data.pk_leaser
                }
              })
            } else {
              this.$toast(res.data.message);
            }
          })
        } else {
          if (this.parms.username==null){
            this.nametext="账号不能为空！"
          } else {
            this.pastext="密码不能为空！"
          }
        }
    }
  }
}



